*{
  margin: 0;
  padding: 0;
}

.logo{
  display: flex;
  align-items: center;
  padding: 10px;
  height: 10vh;
  box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.17), 0px 6px 20px 0px rgba(0, 0, 0, 0.20)

}
.logoPng{
  height: 7vh;
}
.hero{
  margin-top: 20px;
  padding: 20px;
}
.upper{
  display: flex;
  justify-content: center;
  align-items: center;
}
.lower p{
  padding-left: 10px;
}
.class-list {
  display: flex;
  /* background-color: red; */
  flex-wrap: nowrap;
  gap: 110px;
  overflow-x: auto;
}
.cards{
  /* display: flex; */
  /* background-color: aqua; */
  /* justify-content: center; */
  /* align-items: center; */
  flex-direction: column;
  padding: 10px 20px;

}
.card{
  width: 175px;
  font-size: 12px;
  flex: 0 0 auto;         /* ADD */
  display: flex;          /* ADD */
  flex-direction: column; /* ADD */
  position: relative;

}
.card-logo img{
  /* width: 30vw; */
  width: auto;
  height: 50vh;
}
.gray{
  color: grey;
}

.badge {
  background-color: red;
  position: absolute;
  top: 6px;
  left: 6px;
  background-color: white;
  padding: 5px 7px;
  border-radius: 2px;
  font-weight: bold;
}

